export default {
	data() {
		return {
			size:'',
			data:[],
			max: 0,
			loadMoreText: "加载中...",
			showLoadMore: true,
			tableList:[],
			value1:'',
			value2:''
		}
	},
	onLoad() {
		this.dk = this.websiteUrl
		uni.getStorage({
			key:'fwqsj',success: res => {
				this.dk = res.data
				this.initDate()
			}
		})
	},
	onUnload() {
		this.max = 0,
		this.data = [],
		this.loadMoreText = "加载更多",
		this.showLoadMore = false;
		console.log('onUnload事件');
	},
	onReachBottom() {
		console.log("onReachBottom");
		console.log('max：'+this.max );
		if (this.max >= this.size) {
			this.loadMoreText = "没有更多数据了!"
			return;
		}
		this.showLoadMore = true;    //设置下拉提示的标记
		setTimeout(() => {
			this.setDate();
		}, 500);
	}, 
	methods: {
		initDate(){
			uni.request({
				url: `https://${this.dk}/getEmployeeInfo.aspx`,
				method: 'GET',
				data: {
					uid:this.value1,
					uname:this.value2,
				},
				success: res => {
					if(res.data.length>8000){
						uni.showModal({
							title:'友情提示',
							content:'您的数据量过大，请到电脑端查询'
						})
					}
					this.tableList = res.data
					this.size = this.tableList.length
					let temp = res.data
					let ids = ''
					let uname = ''
					for (let i = 0;i <temp.length;i++) {						
						ids = ids + temp[i].UID
						uname = uname + temp[i].uname
					}
					this.value1 = ''
					this.value2 = ''
					//一下为下拉使用
					
					this.max = 0;
					this.data = [];
					let data = [];
					this.max += (this.size<=20?this.size:20);
					let temp1 = this.tableList.splice(0,20);
					for(var i = 1;i <= temp1.length;i++){
						data.push(temp1[i-1]);
					}
					this.data = this.data.concat(data);
					uni.stopPullDownRefresh();
				}
			});
		},
		setDate() {
			let data = [];
			this.max += (Math.floor(this.tableList.length/20)>0?20:this.tableList.length);
			let temp1 = this.tableList.splice(0,20);
			for(var i = 1;i <= temp1.length;i++){
				data.push(temp1[i-1]);
			}
			this.data = this.data.concat(data);
		},
		onpeDetailed(){
			this.initDate()
		}
	},
}
